<template>
  <transition name="slide-menu">
  <div class="slidemenu" v-show="visible" :value="value.isShow"   >
  	<div class="slidemenu-conent" >
      <div class="login-header">
        <div class="top-back" @click="goBack">
          <i class="fa fa-angle-left" style="padding-left:12px;"></i>
        </div>
        <div class="center">
          查询
        </div>
      </div>
          <div class="search-input"> <input type="text" placeholder="证券代码"  v-model="hqzqdm" @click="fliterStk(hqzqdm)"/></div>
          <ul class="my-hold-container">
                <li v-for="(item,index) in stkList" @click="selectedStk(index)">
                   <div><div>{{item.xxzqdm}}</div></div>
                   <div class="text-right"><div>{{item.xxzqjc}}</div></div>
                   <!-- 支持中文简称<div class="text-right"><div>{{item.stkAvl+item.stkTrdFrz}}</div><div>{{item.stkAvl}}</div></div>  -->
                </li>
            </ul>
   </div>
  </div>
  </transition>
</template>

<script>

export default {
  name: 'slidemenu',
  data () {
    return {
      isShow2:true,
      visible:false,
      myDb:'',
      col:'',
      menuName:'',
      menuList:[],
      pdname:'',
      pddata:'',
      hqzqdm:'',
      stkList:[],
      stkListAll:[],
    }
  },
  props:{
      value:{
        type:Object,
        default:function() {
        return {
          isShow:false
        };
        }
      }
    },
  computed:{

  },
  created:function(){
    this.getUpCltStkQry();
  },
  watch:{
       value(val) {
            this.visible = val.isShow;
          },
           visible(val) {
            this.value.isShow=true;
          },
          hqzqdm(val){
            if(val!=''){
             this.fliterStk(val);// xxzqdm
            }else{
              this.stkList=[];
            }
          }
    },
  methods:{
    goBack:function(){
       this.visible=false;
    },
    selectedStk:function(index){
      this.$emit("callback",this.stkList[index])
      this.visible=false;
    },
    hide:function(){
      this.visible=false;
    },
    fliterStk:function(val){
      var stks=[];
      this.stkListAll.forEach(function(item,index){
          if(item.xxzqdm.indexOf(val)!=-1){
            stks.push(item);
          }
      });
      this.stkList=stks;
    },
    getUpCltStkQry:function(){//获取证券信息
            var self=this;
                axiosUtil.upCltStkQry({xxjyms:global.trdMode.jhjj,xxzqzt:global.ZQZT.YXSC+","+global.ZQZT.PTSC}).then(data=>{//竞价模式
                    var data=data.data;
                    console.log(data);
                    if(data && data.total && data.total!=0){
                        this.stkListAll=data.rows;
                    }

            });
        },

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.slidemenu{
/*	height: 100%;
	width: 100%;
	position: fixed;
	top: 0;
  left:0;
	background-color: rgba(0,0,0,.3);
	z-index: 998;
  transition: 3s all ease;*/
  position: fixed;
      width: 100%;
      height: 100%;
      z-index: 999;
      left: 0;
      top: 0;
        background-color: rgba(0,0,0,.3);
    transition: .3s all ease;
}
.slidemenu-conent{
	height: 100%;
	width: 100%;
	background: #fff;
}
.menu-list{
  color: #111;
}
.search-input{
  margin-top: 48px;
      background: #ddd;
    padding: 12px;
    text-align: center;
}
.search-input input {
  height: 28px;
  border: 1px solid #ff5256;
  width: 100%;
}
.my-hold-container li{
  height: 38px;
  line-height: 24px;
}
.my-hold-container li >div{
  width: 50%;
}

.login-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: #ef4f4f;
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
}
.my-hold-container {
  width: 100vw;
}
</style>



