<template>
<div class="buyin-contain">
    	<custHeader :title="title"></custHeader>
      <i class="fa fa-refresh" aria-hidden="true" style="    position: fixed;top: 8px;right: 12px;color: #fff; z-index: 99999;font-size: 24px;" @click="refreshHq"></i>
  <div class="opertion-hq" style="    display: flex;">
    <div class="opertion-zone opertion-zone-sell">
      <div v-if=false> 证券账户<span>{{trdAcct}}</span> </div>

      <div style="position:relative"> <input :style="{'textAlign':zqdmTextAlign,'paddingLeft':'6px'}" type="text" placeholder="请输入证券代码" v-model="hqzqdm" @click="searchStkex" />
        <div v-show="hqzqjc!=''" style="position:absolute;right:6px;top:6px;font-size:14px;">{{hqzqjc}}</div>
      </div>
      <numberBox :pars="pricePars" placeholder="价格" @callback="getMaxQty" class="opt-button" v-model="orderPrice"></numberBox>
      <div class="normal-box" style="font-size:12px;">
           <div class="t-left ">跌停:<span class="sub-color">{{xxdtjg | toMoney}}</span></div>
          <div class="t-right ">涨停:<span class="add-color">{{xxztjg | toMoney}}</span></div>
      </div>
      <numberBox :pars="numPars" placeholder="申报数量" class="opt-button" v-model="orderQty"></numberBox>
      <div class="opt-title"> 可卖<span>{{maxnum}}</span>股<span v-remoteoptionsspan="dicts.stkStatus" style="float:right;font-size:12px;color:#666;display:none;">{{stkStatus}}</span></div>
      <button type="button" @click="entrust" class="trade-button">卖出</button>
    </div>
    <div class="hq-zone">
      <ul>
        <li class="normal-box " v-for="(item,index) in mmdl" :class="[index==5? zjj:'']">
          <div>{{item.name}}</div>
          <div>{{item.price}}</div>
          <div>{{item.num}}</div>
        </li>
      </ul>
    </div>
  </div>
  <div class="hold-con">
    <myHold @listCallBack="listCallBack" ref="holdRef"></myHold>
  </div>
  <searchComponent class="cover-transition" v-model="searchComponentShow" @callback="initStk"></searchComponent>
</div>
</template>
<script>
import cookieUtil from 'js-cookie'
import {
  MessageBox
} from 'mint-ui'
import searchComponent from 'components/searchComponent.vue'
import numberBox from 'components/numberBox.vue'
import myHold from 'components/myHold.vue'
import custHeader from  'components/custHeaderNoBack'
export default {
  data() {
    return {
        title:"卖出",
      dicts: {
        stkStatus: {
          name: 'STK_STATUS',
          dataKey: "stkStatus"
        },
      },
      zjj: "zjj",
      numPars: {
        toFixedNumber: 0,
        minNumber: 10,
        maxNumber: 100,
        unitNumber: 6,
        val: ""
      },
      pricePars: {
        toFixedNumber: 2,
        minNumber: 10,
        maxNumber: 100,
        unitNumber: 0.01,
        val: ""
      },
      searchComponentShow: {
        isShow: true
      },
      conferNo: "",
      selected: "",
      bb: true,
      trdAcct: "", //证券账户
      hqzqdm: "", //证券代码
      hqzqjc: "", //证券简称
      maxnum: "", //
      xxztjg:"",//涨停价格
      xxdtjg:"",//
      xxjzjg:"",//基准价格
      xxjgdw:"",
      xxsldw:"",//
      xxzxsbsl:"",//最小申报数量
      orderQty: "", //
      orderPrice: '',
      stkStatus: 'C',
      bargainOrderPrice: '', //成交价格
      bargainOrderQty: '', //成交数量
      pars: { //请竞价行情参数
        epsBgn: "",
        epsEnd: "",
        hqzqdm: "",
        industry: "",
        offset: 0,
        pageSize: global.pageSize,
        recNo: 1,
        regCapitalBgn: "",
        regCapitalEnd: "",
        region: "",
        sortFields: "",
        xyscdm: global.scdm,
        xyywlb: global.STKBIZTYPE.SELL_OUT, //定价买入业务类别
      },
      mmdl: [{
          name: "卖5",
          price: "-",
          num: "-"
        },
        {
          name: "卖4",
          price: "-",
          num: "-"
        },
        {
          name: "卖3",
          price: "-",
          num: "-"
        },
        {
          name: "卖2",
          price: "-",
          num: "-"
        },
        {
          name: "卖1",
          price: "-",
          num: "-"
        },
        {
          name: "买1",
          price: "-",
          num: "-"
        },
        {
          name: "买2",
          price: "-",
          num: "-"
        },
        {
          name: "买3",
          price: "-",
          num: "-"
        },
        {
          name: "买4",
          price: "-",
          num: "-"
        },
        {
          name: "买5",
          price: "-",
          num: "-"
        },
      ]

    }
  },
  created: function() {
    //    axiosUtil.isLogin().then(message=>{
    //         var message=message.data;
    //       if(message.errorcode==0){//是否登录
    this.trdAcct = cookieUtil.get("trdAcct");
    if (this.$route.query.hqzqdm) {
      this.hqzqdm = this.$route.query.hqzqdm;
    }
    if (this.$route.query.conferNo) {
      this.conferNo = this.$route.query.conferNo
    }
    if (this.$route.query.bargainOrderPrice) {
      this.bargainOrderPrice = this.$route.query.bargainOrderPrice
    }
    if (this.hqzqdm != '') {
      this.getUpCltStkQry.bind(this)();
    }
    //this.marketJj();

    //     }else{
    //         console.log("no")
    //         this.$router.push("/login");
    //     }
    //    })
  },
  components: {
    searchComponent,
    numberBox,
    myHold,
     custHeader
  },
  computed: {
    zqdmTextAlign: function() {
      return this.hqzqdm === '' ? 'center' : 'left';
    }
  },
  methods: {
    refreshHq:function(){
        this.getUpCltStkQry();
    },
    listCallBack: function(item) {
      this.hqzqdm = item.stkCode;
      this.getUpCltStkQry();
     // this.marketJj();
    },
    initStk: function(val) {
       this.hqzqdm = val.xxzqdm;
      this.hqzqjc = val.xxzqjc;
      this.getUpCltStkQry();
      //this.marketJj();
    },
    marketYx: function() {
      let self = this;
      this.pars.hqzqdm = this.hqzqdm;
      if (this.hqzqdm != "") {
          axiosUtil.marketYx(this.pars).then(function(data) {
            var data = data.data;
            //self.dataListJj= data.rows;
            var item = data.rows[0];
            if (data.rows.length !== 0) {
              self.mmdl = [];
              for (let i = 5; i >= 1; i--) {
                self.mmdl.push({
                  name: "卖" + i,
                  price: item["hqsjw" + i],
                  num: item["hqssl" + i],
                });
              }
              for (let i = 1; i <= 5; i++) {
                self.mmdl.push({
                  name: "买" + i,
                  price: item["hqbjw" + i],
                  num: item["hqbsl" + i],
                });
              }
            }


            if (data.rows.length == 0) {
              // self.tipMessage="暂无数据"
            }
          })
      }

    },
    marketPt: function() {
      let self = this;
      this.pars.hqzqdm = this.hqzqdm;
      if (this.hqzqdm != "") {
          axiosUtil.marketPt(this.pars).then(function(data) {
            var data = data.data;
            //self.dataListJj= data.rows;
            var item = data.rows[0];
            if (data.rows.length !== 0) {
              self.mmdl = [];
              for (let i = 5; i >= 1; i--) {
                self.mmdl.push({
                  name: "卖" + i,
                  price: item["hqsjw" + i],
                  num: item["hqssl" + i],
                });
              }
              for (let i = 1; i <= 5; i++) {
                self.mmdl.push({
                  name: "买" + i,
                  price: item["hqbjw" + i],
                  num: item["hqbsl" + i],
                });
              }
            }


            if (data.rows.length == 0) {
              // self.tipMessage="暂无数据"
            }
          })
      }

    },
    getMaxQty: function(val) { //求最大申报数量
      //console.log(val)
      var data = {
        stkex: global.scdm,
        stkbd: global.stkbd,
        orderPrice: val,
        stkBiz: global.STKBIZ.SELL_OUT,
        trdacct: this.trdAcct,
        stkCode: this.hqzqdm
      }
      if(this.hqzqdm!="" && (val*1)>0){
          axiosUtil.upCltSellMaxQtyQry(data).then(data => {
            var data = data.data;
            if (data.errorcode == 0) {
              this.maxnum = data.data;
              if((this.maxnum*1)%(this.xxsldw*1)!=0){
                    this.maxnum=Math.floor( (this.maxnum*1)/(this.xxsldw*1))*(this.xxsldw*1);
                } 
              this.numPars = Object.assign(this.numPars, {
                maxNumber: this.maxnum
              })
            } else {
              MessageBox("提示", data.errormsg)


            }
          })
      }

    },
    getUpCltStkQry: function() { //获取证券信息
      var self = this;
       if(this.hqzqdm!=""){
           axiosUtil.upCltStkQry({
             xxzqdm: this.hqzqdm,
             xxjyms: global.trdMode.jhjj
           }).then(data => {
             var data = data.data;
             if (data && data.total && data.total != 0) {
               console.log(data);
               var data = data.rows[0];
               this.stkStatus = data.xxzqzt;
                window.dict(this.dicts.stkStatus.name,data.xxzqzt).then(data=>{
                    this.title = data;
                });
               if(data.xxzqzt=="B"){
                   this.marketYx();
               }else if(data.xxzqzt=="C"){
                   this.marketPt();
               }
               this.xxztjg=data.xxztjg;
               this.xxdtjg=data.xxdtjg;
               this.xxjzjg=data.xxjzjg;
               this.xxsldw=data.xxsldw;
               this.xxjgdw=data.xxjgdw;
               this.xxzxsbsl=data.xxzxsbsl;
               this.pricePars = {
                 toFixedNumber: 2,
                 minNumber: data.xxdtjg, //设置跌停价格
                 maxNumber: data.xxztjg, //设置了涨停价格
                 unitNumber: data.xxjgdw,
                 val: data.xxjzjg //显示基准价格
               };
               this.hqzqjc = data.xxzqjc;
               if (data.xxjzjg != 0) {
                 this.getMaxQty(data.xxjzjg)
               }
               this.numPars = {
                 toFixedNumber: 0,
                 minNumber: data.xxzxsbsl,
                 maxNumber: 1000000,
                 unitNumber: data.xxsldw,
                 val: ""
               };
             }

           });
       }

    },
    searchStkex: function() {
      this.searchComponentShow = {
        isShow: true
      };
    },
    entrust: function() {
      var data = {
        stkex: global.scdm,
        stkbd: global.tkbd, //报价交易板块
        trdacct: this.trdAcct,
        stkCode: this.hqzqdm,
        orderPrice: this.pricePars.val,
        orderQty: this.numPars.val,
        trdMode: global.trdMode.jhjj, //报价交易模式
        stkBiz: global.STKBIZ.SELL_OUT
      }
      if(this.hqzqdm==""){
          MessageBox("提示","证券信息不准为空!");
          return ;
      }
      if(this.pricePars.val==""){
          MessageBox("提示","价格不准为空!");
          return ;
      }
      if(this.numPars.val==""){
          MessageBox("提示","数量不准为空!");
          return ;
      }
      if(this.pricePars.val*1>this.xxztjg || this.pricePars.val*1<this.xxdtjg){
          MessageBox("提示","价格不能大于涨停价格，不能小于跌停价格");
          return ;
      }
      if((this.pricePars.val*1*100)%(this.xxjgdw*100)!=0){
          MessageBox("提示","价格必须是基准价格("+this.xxjgdw+")的整数倍");
          return ;
      }
      if(this.numPars.val*1>this.maxnum ){
          MessageBox("提示","数量不能大于最大可卖("+this.maxnum +")");
          return ;
      }
      // if((this.numPars.val*1)%this.xxsldw!=0){
      //     MessageBox("提示","数量必须是卖量单位("+this.xxsldw+")的整数倍");
      //     return ;
      // }
      axiosUtil.sellOutopre(data).then(data => {
        //    console.log(data);
        var data = data.data;

        if (data.errorcode == 1) {
          MessageBox("提示", data.errormsg)
        } else {
          this.$refs.holdRef.getMyHold();
          MessageBox("提示", "委托成功，委托号=" + data.data)
        }
      });

    }
  }
}
</script>
<style scoped>
.buyin-contain {
  flex-direction: column;
  display: flex;
  height: 100%;
  padding-bottom: 26px;
}

.hold-con {
  flex-grow: 1;
  background: #fff;
  margin-top: 12px;
}
</style>



